.infotooltip__container {
  max-width: 430px;
  background: #ffffff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  position: relative;
  z-index: 1;
  display: flex;
  padding: 60px 36px;
  flex-direction: column;
  box-sizing: border-box;
}

@media screen and (max-width: 450px) {
  .infotooltip__container {
    width: 90%;
    margin: 40px auto 0;
    padding: 50px 18px;
  }
}
